import React from "react";
import {
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
  Typography,
  Box,
} from "@mui/material";
import { Link } from "react-router-dom";
import HomeIcon from "@mui/icons-material/Home";
import GroupIcon from "@mui/icons-material/Group"; // 

const Menu = ({ open, toggleMenu }) => {
  return (
    <Drawer
      anchor="left"
      open={open}
      onClose={toggleMenu}
      sx={{ "& .MuiDrawer-paper": { width: 250, backgroundColor: "#f5f5f5" } }}
    >
      <Box sx={{ p: 2, backgroundColor: "#3f51b5", color: "white" }}>
        <Typography variant="h6" noWrap>
          
        </Typography>
      </Box>
      <Divider />
      <List>
        <ListItem button component={Link} to="/" onClick={toggleMenu}>
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem>
        {/* <ListItem button component={Link} to="/accessories" onClick={toggleMenu}>
          <ListItemIcon>
            <BuildIcon />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem> */}
        <ListItem button component={Link} to="/weapons" onClick={toggleMenu}>
          <ListItemIcon>
            <img
              src={"../img/.png"}
              alt=""
              style={{ width: 24, height: 24 }}
            />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem>
        <ListItem button component={Link} to="/armors" onClick={toggleMenu}>
          <ListItemIcon>
            <img
              src={"../img/.png"}
              alt=""
              style={{ width: 24, height: 24 }}
            />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem>
        <ListItem
          button
          component={Link}
          to="/accessories"
          onClick={toggleMenu}
        >
          <ListItemIcon>
            <img
              src={"../img/.png"}
              alt=""
              style={{ width: 24, height: 24 }}
            />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem>
        <ListItem
          button
          component={Link}
          to="/partymanagement"
          onClick={toggleMenu}
        >
          <ListItemIcon>
            <GroupIcon />
          </ListItemIcon>
          <ListItemText primary="" />
        </ListItem>
      </List>
    </Drawer>
  );
};

export default Menu;
