import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Table,
  TableBody,
  TableCell,
  TableRow,
  TextField,
  TableHead,
} from "@mui/material";
import weapon from "../jsons/weapon.json";

const WeaponPage = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [effectTerm, setEffectTerm] = useState("");
  const [elementFilters, setElementFilters] = useState([]);
  const [roleFilters, setRoleFilters] = useState([]);
  const [filteredWeapons, setFilteredWeapons] = useState(weapon);

  useEffect(() => {
    setFilteredWeapons(
      weapon.filter(
        (w) =>
          w.name.toLowerCase().includes(searchTerm.toLowerCase()) &&
          w.effect.toLowerCase().includes(effectTerm.toLowerCase()) &&
          (elementFilters.length === 0 || elementFilters.includes(w.element)) &&
          (roleFilters.length === 0 || roleFilters.includes(w.role))
      )
    );
  }, [searchTerm, effectTerm, elementFilters, roleFilters]);

  const toggleElementFilter = (element) => {
    setElementFilters((prev) =>
      prev.includes(element)
        ? prev.filter((e) => e !== element)
        : [...prev, element]
    );
  };

  const toggleRoleFilter = (role) => {
    setRoleFilters((prev) =>
      prev.includes(role) ? prev.filter((r) => r !== role) : [...prev, role]
    );
  };

  return (
    <Container>
      <Box my={4}>
        <TextField
          label=""
          variant="outlined"
          fullWidth
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </Box>
      <Box my={4}>
        <TextField
          label=""
          variant="outlined"
          fullWidth
          value={effectTerm}
          onChange={(e) => setEffectTerm(e.target.value)}
        />
      </Box>
      <Box sx={{ display: "flex", flexWrap: "wrap", gap: 2, mb: 2 }}>
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: roleFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleRoleFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: roleFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleRoleFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: roleFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleRoleFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: roleFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleRoleFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
        <img
          src={"../img/.png"}
          alt=""
          style={{
            border: "1px solid black",
            borderRadius: "8px",
            backgroundColor: elementFilters.includes("")
              ? "#00BFFF"
              : "transparent",
          }}
          onClick={() => toggleElementFilter("")}
        />
      </Box>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredWeapons.map((w) => (
            <TableRow key={w.no}>
              <TableCell>{w.name}</TableCell>
              <TableCell>{w.atk}</TableCell>
              <TableCell>{w.matk}</TableCell>
              <TableCell>{w.effect}</TableCell>
              <TableCell>{w.element}</TableCell>
              <TableCell>{w.role}</TableCell>
              <TableCell>{w.remark}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Container>
  );
};

export default WeaponPage;
